.loading {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 50px;
    height: 20px;
  }
  
  .loading > div {
    background: rgb(150, 150, 150);
    width: 7px;
    height: 7px;
    border-radius: 50%;
    animation: loading 0.6s infinite;
  }
  
  .loading > div:nth-child(2) {
    animation-delay: 0.2s;
}
  
.loading > div:nth-child(3) {
    animation-delay: 0.4s;
}
  
@keyframes loading {
    0% {
        transform: translateY(2px);
    }
    50% {
        transform: translateY(-2px);
    }
    100% {
        transform: translateY(2px);
    }
}