@keyframes checkmark {
  0% {
    width: 0;
  }
  100% {
    width: 7px;
  }
}

@keyframes checkmark_kick {
  0% {
    height: 0;
  }
  100% {
    height: 10px;
  }
}

.checkmark {
  position: relative;
  height: 10px;
  width: 10px;
  display: block;
}

.checkmark::before,
.checkmark::after {
  content: "";
  position: absolute;
  height: 1.5px;
  background-color: #1d4cf8;
  opacity: 0;
}

.checkmark::before {
  top: -5px;
  left: -6px;
  width: 0;
  transform: rotate(50deg);
  animation: checkmark 0.1s forwards;
  border-radius: 1px;
}

.checkmark::after {
  top: 0px;
  left: 0px;
  height: 0;
  width: 1.5px;
  transform: rotate(220deg);
  transform-origin: top left;
  animation: checkmark_kick 0.1s forwards 0.1s;
  border-radius: 1px;
}

.animate::before,
.animate::after {
  opacity: 1;
  animation-play-state: running;
}

